import { Link } from 'react-router-dom'

export const AccueilBanner = () => {
    return <div className="accueilBanner">
        <section className="accueilBanner-text">
            <h1>En quelque clic,<br /> trouvez le travail ou le job de vos rêve</h1>
            <form className="accueilBanner-form">
                <input placeholder="Secteur d'activités ..." />
                <input placeholder="Region..." />
                <div>
                    <button className='semiRounded'>Chercher</button>
                    <Link className='semiRounded' to='/'>S'inscrire</Link>
                </div>
            </form>

        </section>
        <section className='accueilBanner-img'>

        </section>
        <img src={require('../Assets/images/illustrations/banner2.svg').default} />
    </div>
}


export const TypeServices = () => {
    return <div className='accueilTypeServices'>
        <section className='accueilTypeService'>
            <img src={require('../Assets/images/illustrations/employeur.svg').default} />
            <div>
                <h2>Vous êtes un employeur ?</h2>
                <p>Publiez une offre et trouvez le profil dont vous avez besoin</p>
                <Link to="/">S'inscrire</Link>
            </div>
        </section>

        <section className='accueilTypeService'>
            <img src={require('../Assets/images/illustrations/worker.svg').default} />
            <div>
                <h2>Vous êtes a la recherche d'emplois ?</h2>
                <p>Déposez votre CV et décrochez votre prochain emploi</p>
                <Link to='/'>S'inscrire</Link>
            </div>
        </section>
    </div>
}