import { Link } from "react-router-dom"

export const Nav = () => {

    const handleMobileMenu = () => {
        document.querySelector('.nav-links').classList.toggle('nav-links-visible')
    }

    return <nav>
        <div className="nav-logo">
            <img src={require('../Assets/images/logo4.png').default} />
        </div>

        <div className="nav-mobileBtn">
            <i className="mdi mdi-menu" onClick={() => handleMobileMenu()}></i>
        </div>

        <div className="nav-links">
            <Link to="/">Accueil</Link>
            <Link to="/">A propos</Link>
            <Link to="/Contact">Contact</Link>
            <div className="nav-authLinks">
                <Link className="semiRounded" to="/">Connexion</Link>
                <Link className="semiRounded" to="/">Inscription</Link>
            </div>
        </div>

    </nav>
}